import React from "react"
import { Link, graphql } from "gatsby"
import Layout from "../components/layout"
import { css } from "@emotion/core"
import styles from "../styles/blog-post-module.module.css"

export default ({ data }) => {
  const post = data.markdownRemark

  return (
    <Layout>
      <div id={styles.container}>
        <a href="javascript:history.back()">&lt;&lt; go back</a>

        <hr />
        <p className={styles.title}>{post.frontmatter.title}</p>
        <p className={styles.url}>
          https://flik.github.io/f=log{post.fields.slug}
        </p>
        <p className={styles.date}>{post.frontmatter.date}</p>

        <style>
          {`
            *{
             line-height: 1.5rem;
            }
            code {
            font-size: 0.85rem;
            line-height: 1.4rem;
            background-color: rgb(250,250,255);
            border: solid 1px rgb(230,230,230);
            border-radius: 6px;
            display: block;
            margin: 8px 0 8px 0;
            padding: 8px 8px 12px 4px;
          }`}
        </style>
        <div dangerouslySetInnerHTML={{ __html: post.html }} />

        <div className={styles.credit}>
          <div>
            {post.frontmatter.title}
            <br />
            {post.frontmatter.date}
            <br />
            {post.parent.name}
            {post.parent.ext}
          </div>
        </div>
        <hr />
        <text size="12px">
          <center>Copyright by Flik(정성훈) </center>{" "}
        </text>
      </div>
    </Layout>
  )
}

export const query = graphql`
  query($slug: String!) {
    markdownRemark(fields: { slug: { eq: $slug } }) {
      html
      frontmatter {
        title
        date(formatString: "DD MMMM, YYYY")
      }
      fields {
        slug
      }
      parent {
        ... on File {
          name
          ext
        }
      }
    }
  }
`
